import React from 'react'
import { BsFillCheckCircleFill } from 'react-icons/bs'
import ServiceCard from './ServiceCard'
import sr1 from './images/sr1.jpg'
import home from '../../Components/Media/home.png'
import water2 from '../../Components/Media/water2.png'
import ac4 from '../../Components/Media/ac4.png'
import team from '../../Components/Media/team.png'

function Header3() {
    return (
        <>
            <div className='  grid grid-cols-12 md:px-20 sm:py-6 bg-indigo-50'>
                <div className='col-span-12 sm:mb-8'>
                    <div className='text-2xl md:text-3xl font-bold mt-1 text-center md:w-1/2 mx-auto text-gray-800'>E-Gov Services</div>
                </div>
                <div className="col-span-12 md:col-span-3 px-10 sm:px-12 2xl:px-14 mt-4 md:mt-0">
                    <ServiceCard action={'login'} logo={home} heading={'Property Tax Management'} 
                    description={"Property assessment is the process of determining the value of a property for taxation purposes."} 
                    />
                </div>
                <div className="col-span-12 md:col-span-3 px-10 sm:px-12 2xl:px-14 mt-4 md:mt-0">
                    <ServiceCard action={'login'} logo={water2} heading={'Water User Charges Management System'} 
                    description={"Efficiently track and manage water user charges with our automated Water User Charges Management System."}
                    />
                </div>
                <div className="col-span-12 md:col-span-3 px-10 sm:px-12 2xl:px-14 mt-4 md:mt-0">
                    <ServiceCard action={'login'} logo={team} heading={'Municipal Trade License Fee Management System'} 
                    description={"Simplify the process of managing municipal trade license fees with user-friendly and automated management system."} 
                    />
                </div>
                <div className="col-span-12 md:col-span-3 px-10 sm:px-12 2xl:px-14 mt-4 md:mt-0">
                    <ServiceCard action={'login'} logo={ac4} heading={'Advertisement Tax Management System'} 
                    description={"Streamline the management of advertisement taxes with automated Advertisement Tax Management System, saving time and increasing accuracy."} 
                    />
                </div>

            </div>
        </>
    )
}

export default Header3