import React from 'react'

const AdminCard = (props) => {
  return (
    <>
    
    <div className='relative flex gap-x-4 bg-white rounded-md shadow-md  hover:scale-105 transition-all duration-200 ease-in-out h-[20vh] pr-2'>

        {/* <div className='z-0 h-full bg-gradient-to-b from-indigo-600 to-white'></div> */}

       <div className='bg-indigo-500 rounded-l-md p-4 flex flex-col justify-center gap-y-2'>
         {/*========Image==============*/}
         <div className=' flex justify-center items-center'>
              <img src={props?.image} alt="" srcset="" className='w-[10vh] border-2 border-white rounded-full shadow-sm' />
            </div>

        {/* ========Name=========== */}
        <div className='text-white font-semibold text-xs md:text-sm 2xls:text-base text-center'>
            {props?.name}
        </div>
       </div>

        {/* =======Details========== */}
        <div className='flex flex-col gap-y-1 justify-center text-xs md:text-sm 2xls:text-base'>
            <div>
            &#xbb; {props?.post}
            </div>
            <div>
            &#xbb; {props?.place}
            </div>
            <div>
            &#xbb; <a href={`tel:+91${props?.mobile}`}>{props?.mobile}</a>
            </div>
            <div>
            &#xbb;  <a href={`mailto:${props?.email}`}>{props?.email}</a>
            </div>
        </div>

    </div>
    
    </>
  )
}

export default AdminCard