import React from "react";
import { Link, useNavigate } from "react-router-dom";
import hand from './images/hand.png'

const LinkCard = (props) => {

    const navigate = useNavigate()
  return (
    <>
      <div className="poppins flex items-center gap-2 bg-yellow-50 hover:bg-yellow-100 shadow-lg py-2 text-xs 2xls:text-sm transition-all duration-500 md:pl-4 my-2 flex-row flex-wrap justify-between md:pr-6 md:px-0 px-2  cursor-pointer" onClick={() => window.open(`/citizen/${props?.action}`, '_blank')}>
        <div className="poppins">
        <span className="inline w-[10%]"><img src={hand} alt="" className="inline rotate-90 w-5" srcset="" /></span>&nbsp;&nbsp;<span className="2xls:text-sm text-xs">{props?.message}</span>
        </div>
        {/* <button className="rounded-sm bg-indigo-500 hover:bg-indigo-600 text-white transition-all duration-200 ease-in-out px-3 py-[0.2rem] text-xs shadow-md shadow-indigo-200" onClick={() => window.open(`/citizen/${props?.action}`, '_blank')}>
          Go
        </button> */}
      </div>
    </>
  );
};

export default LinkCard;

