import React from 'react'
import { useNavigate } from 'react-router-dom'
import BackendUrl from '../../Components/ApiList/BackendUrl'

const NoticeCard = (props) => {

    const navigate = useNavigate()
    let baseUrl = BackendUrl;

  return (
    <>
    <a href={`${baseUrl}/${props?.action}`} target='_blank'>
        <div className='grid grid-cols-12  gap-x-2 border-2 border-indigo-500 mb-2'>

            <div className='col-span-12 md:col-span-2 bg-indigo-500 p-2 flex items-center justify-center font-semibold text-white'>
                {props?.date}
            </div>

            <div className='col-span-12 md:col-span-10 p-2 items-center'>
                {props?.new && <span className='px-2 py-1 bg-red-600 text-white text-xs md:text-sm scale-90 animate-pulse'>New</span>} &nbsp;
                <span className='md:text-base text-sm'>{props?.content}</span>
            </div>

        </div>
</a>
    </>
  )
}

export default NoticeCard