import React from 'react'
import { useNavigate } from 'react-router-dom'

const ServiceCard = (props) => {

  const navigate = useNavigate()

  return (
    <>
    
        <div className='relative rounded-md shadow-md hover:-translate-y-4 transition-all duration-200 ease-in-out md:h-[18.5rem] 2xl:h-[20rem] bg-white  overflow-hidden'>

            {/* ========LOGO========== */}
            <div className='bg-indigo-500 flex justify-center items-center py-6 2xl:py-8 rounded-t-md '>
              <div className='rounded-full border-2 border-sky-200'>
              <img src={props?.logo} alt="" srcset="" className='w-[8vh] bg-white rounded-full m-1 p-3 shadow-md' />
              </div>
            </div>

            <div className=' flex flex-col items-center justify-center py-2 gap-y-2 px-2 rounded-b-md'>

                {/* ========Heading========= */}
                <div className='font-semibold text-center 2xl:text-base text-sm'>
                  {props?.heading}
                </div>

                {/* ===========Description======== */}
                <div className='flex text-center 2xls:text-sm text-xs'>
                  {props?.description}
                </div>

                {/* ===========Button============ */}
                <div>
                    <button className="rounded-sm bg-indigo-500 hover:bg-indigo-600 text-white transition-all duration-200 ease-in-out px-3 py-[0.2rem] text-xs shadow-md shadow-indigo-200" onClick={() => navigate(`/${props?.action}`)}>Apply</button>
                </div>

            </div>

        </div>
    
    </>
  )
}

export default ServiceCard